.container {
    
    display: block;
    
    width: 100%;
    height: 10rem;
    
    cursor: pointer;
    z-index: 1;
    
    -webkit-transition: all .2s;
    
    -o-transition: all .2s;
    
    transition: all .2s;
    
}

.container:hover {
    
    -webkit-transform: translateX(.5rem) translateY(-.7rem);
    
        -ms-transform: translateX(.5rem) translateY(-.7rem);
    
            transform: translateX(.5rem) translateY(-.7rem);
    -webkit-box-shadow: .4rem .4rem 1rem rgba(0, 0, 0, .7);
            box-shadow: .4rem .4rem 1rem rgba(0, 0, 0, .7);
    z-index: 10;
}

.container img {
    
    display: block;
    width: 100%;
    height: 100%;
    
    -o-object-fit: cover;
    
       object-fit: cover;
    
}


