.selectionButton {
    
    
    grid-row: 1 / 2;
    
    display: -ms-flexbox;
    
    display: flex;
    padding: 1.5rem 0;
    
    
    -ms-flex-pack: center;
    
    
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
    text-align: center;
    
    color: #3b3b3b;  
    font-family: 'Old Standard TT', serif;
    font-size: 2rem;
    
    border-top: .1rem solid #3b3b3b;
    border-bottom: .1rem solid #3b3b3b;
    
    cursor: pointer;
    
    -webkit-transition: all .2s;
    
    -o-transition: all .2s;
    
    transition: all .2s;
    
}


.selectionButton:hover {
    
    
    background-color: #3b3b3b;
    color: white;
    
    
}


